
.page-template{
    margin-top : 5rem;
    margin-left : auto;
    margin-right: auto;
    width: 600px;
    background-color: white;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    padding-top: 2rem;
    border-radius: 10px;

    @media(max-width : 768px){
        margin-top : 1rem;
        width: calc(100%-2rem);
    }

    h1{
        text-align: center;
        font-size: 4rem;
        font-weight: 300;
        margin: 0;
    }

    .content{
        margin-top: 2rem;
    }
}