@import '~open-color/open-color';

.todo-input {
  border-top: 2px solid $oc-blue-5;
  display: flex;
  padding: 1rem;

  input {
    // ì¸í’‹ì˜ ê¸°ë³¸ ìŠ¤íƒ€ì¼ì„ ì§€ìš°ê³  ìƒˆ ìŠ¤íƒ€ì¼ì„ ì •ì˜
    flex: 1; // ë¶€ëª¨ ì—˜ë¦¬ë¨¼íŠ¸ì—ì„œ add-buttonì„ ì œì™¸í•œ ë‚˜ë¨¸ì§€ ê³µê°„ì„ ì°¨ì§€í•©ë‹ˆë‹¤.
    font-size: 1.1rem;
    outline: none;
    border: none;
    background: transparent;
    border-bottom: 1px solid $oc-gray-4;
    &:focus {
      border-bottom: 1px solid $oc-cyan-6;
    }
  }

  .add-button {
    width: 5rem;
    height: 2rem;
    margin-left: 1rem;
    border: 1px solid $oc-blue-7;
    color: $oc-blue-7;
    font-weight: 500;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 20px;
    &:hover {
      background: $oc-blue-7;
      color: white;
    }
    &:active {
      background: $oc-blue-8;
    }
  }
}
