@import '~open-color/open-color';

.todo-item {
  padding: 1rem;
  display: flex;
  align-items: center;
  cursor: pointer;

  .tick {
    margin-right: 1rem;
  }

  .text {
    flex: 1;
    word-break: break-all;
    &.done {
      text-decoration: line-through;
      color: $oc-red-7;
    }
  }

  .delete {
    margin-left: 1rem;
    color: $oc-pink-7;
    font-size: 0.8rem;
    border : 1px solid $oc-pink-5;
    width: 50px;
    height: 20px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      background-color: $oc-pink-7;
      color: white;
    }
  }

  &:nth-child(odd) {
  // í™€ìˆ˜ ë²ˆì§¸ ì—˜ë¦¬ë¨¼íŠ¸ì—ëŠ” íšŒìƒ‰ ë°°ê²½
    background: $oc-gray-0;
  }

  &:hover {
    background: $oc-gray-1;
  }
}

